import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import styled from '@emotion/styled'
import Divider from '@mui/material/Divider';

class StCard extends StreamlitComponentBase {
  public componentDidUpdate(): void {
    Streamlit.setFrameHeight()
  }

  public componentDidMount(): void {
    Streamlit.setFrameHeight(2000)
  }

  public render = (): ReactNode => {
    const { title, section_1,section_2,section_3, footer } = this.props.args;
    const { theme } = this.props
    
    if (!theme) {
      return <div>Theme is undefined, please check streamlit version.</div>
    }

    const height = 250
    const width = 300
    const margin = 40

    const Parent = styled.div({
      height: height + margin * 2,
      width: width + margin * 2,
      margin: 'auto',
    })

    return (
      <Parent>        
        <Card style={{ width: '25rem' }}>
          <Card.Header className="text-center">{title}</Card.Header>
          <ListGroup variant="flush">
              <ListGroup.Item>{section_1}</ListGroup.Item>
              <ListGroup.Item>{section_2}</ListGroup.Item>
              <ListGroup.Item>{section_3}</ListGroup.Item>
          </ListGroup>
          <Card.Footer className="text-center" >
            <Card.Text className="text-muted">
              {footer}
            </Card.Text>
          </Card.Footer>          
        </Card>                                   
      </Parent>
    )
  }

  private onClick = (): void => {
    Streamlit.setComponentValue(true)
  }
}

export default withStreamlitConnection(StCard);